*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: local("Roboto Regular"), local("Roboto-Regular"), url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.js-focus-visible :focus:not(.focus-visible) {
  outline: none; }

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-weight: 500;
  font-family: inherit;
  background-color: transparent;
  border: none; }
  .button:hover {
    cursor: pointer; }
  .button--secondary {
    padding: 8px 16px;
    font-size: 12px;
    line-height: 16px;
    border-radius: 22px; }
  .button--yes {
    color: #64b851;
    background-color: #f2faf0; }
    .button--yes::before {
      content: "";
      width: 6px;
      height: 12px;
      margin-right: 14px;
      border-right: 2px solid #64b851;
      border-bottom: 2px solid #64b851;
      border-radius: 2px;
      transform: translate(2px, -2px) rotate(45deg); }
  .button--no {
    position: relative;
    padding-left: 37px;
    color: #eb4b51;
    background-color: #faf0f0; }
    .button--no::before, .button--no::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 21px;
      width: 2px;
      height: 14px;
      background-color: #eb4b51;
      border-radius: 2px; }
    .button--no::before {
      transform: translateY(-50%) rotate(45deg); }
    .button--no::after {
      transform: translateY(-50%) rotate(-45deg); }
  .button--primary {
    width: 100%;
    padding: 12px;
    font-size: 14px;
    line-height: 20px;
    color: #383736;
    background-color: #fac62d;
    border-radius: 6px; }
    .button--primary::before {
      content: "";
      flex-shrink: 0;
      width: 16px;
      height: 16px;
      margin-right: 8px;
      background: url("../img/icon-key.svg") center/cover no-repeat; }
    .button--primary:hover {
      background-color: #f0b505; }
    .button--primary:active {
      background-color: #e1ab08; }
  .button--close {
    position: relative;
    width: 24px;
    height: 24px; }
    .button--close::before, .button--close::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 19px;
      background-color: #b2b0ab;
      border-radius: 2px; }
    .button--close::before {
      transform: rotate(45deg); }
    .button--close::after {
      transform: rotate(-45deg); }
    .button--close:hover {
      background-color: #f5f4f2;
      border-radius: 4px;
      box-shadow: 0 0 0 2px #f5f4f2; }
      .button--close:hover::before, .button--close:hover::after {
        background-color: #383736; }
    .button--close:active {
      background-color: #e7e4de;
      border-radius: 4px;
      box-shadow: 0 0 0 2px #e7e4de; }
      .button--close:active::before, .button--close:active::after {
        background-color: #383736; }

.page {
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #e5e5e5; }

.page__body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  min-height: 100%;
  padding: 40px 8px; }
  @media (min-width: 768px) {
    .page__body {
      padding: 40px 28px; } }
  @media (min-width: 1440px) {
    .page__body {
      padding: 40px 120px;
      max-width: 1440px;
      margin: 0 auto; } }

.flat {
  position: relative;
  padding: 16px 16px 20px 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 24px rgba(0, 0, 0, 0.04); }
  @media (min-width: 768px) {
    .flat {
      padding: 24px 28px 24px 32px; } }
  @media (min-width: 920px) {
    .flat {
      display: flex;
      justify-content: flex-start;
      padding: 24px 0; } }

.flat__desc {
  display: grid;
  padding-bottom: 20px;
  border-bottom: 1px solid #f0eeeb; }
  @media (min-width: 768px) {
    .flat__desc {
      grid-template-columns: 178px 1fr;
      align-items: flex-start;
      padding-bottom: 24px;
      column-gap: 24px; } }
  @media (min-width: 920px) {
    .flat__desc {
      flex-grow: 1;
      padding: 0 24px;
      column-gap: 32px;
      border-right: 1px solid #f0eeeb;
      border-bottom: none; } }

.flat__title {
  margin: 0 0 16px;
  font-weight: 700;
  font-size: 20px;
  line-height: 28px; }
  @media (min-width: 768px) {
    .flat__title {
      grid-column: 2/3;
      margin: 0 0 8px;
      font-size: 24px;
      line-height: 36px; } }

.flat__img-wrap {
  order: -1;
  margin: 10px 0 24px;
  padding-right: 24px;
  text-align: center; }
  @media (min-width: 768px) {
    .flat__img-wrap {
      grid-row: 1/4;
      margin: 0;
      padding: 0; } }

.flat__img {
  width: 86px;
  height: 160px;
  margin-left: 20px;
  vertical-align: top; }
  @media (min-width: 768px) {
    .flat__img {
      margin: 10px 16px 0 0; } }
  @media (min-width: 920px) {
    .flat__img {
      margin: 10px 0 0; } }

.flat__table {
  margin-bottom: 18px; }
  @media (min-width: 768px) {
    .flat__table {
      grid-column: 2/3; } }

.flat__answers {
  margin: 0;
  padding: 0; }
  @media (min-width: 768px) {
    .flat__answers {
      grid-column: 2/3; } }

.flat__purchase {
  padding-top: 20px; }
  @media (min-width: 768px) {
    .flat__purchase {
      padding-top: 24px; } }
  @media (min-width: 920px) {
    .flat__purchase {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      min-width: 332px;
      padding: 0 32px; } }

.flat__text {
  margin: 0 0 13px;
  font-size: 14px;
  line-height: 20px;
  color: #989691; }
  .flat__text:last-of-type {
    margin: 0 0 13px; }
  @media (min-width: 768px) {
    .flat__text:last-of-type {
      margin: 0 0 23px; } }
  @media (min-width: 920px) {
    .flat__text:last-of-type {
      margin: 0 0 auto; } }

.flat__text-offset {
  display: inline-block;
  margin-bottom: 3px; }

.flat__time {
  font-weight: 500;
  color: #2794db; }

.flat__price {
  text-transform: uppercase; }
  .flat__price--less {
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #383736; }
    @media (min-width: 768px) {
      .flat__price--less {
        font-size: 24px;
        line-height: 36px; } }
  .flat__price--more {
    font-weight: 500;
    color: #eb4b51; }

.flat__close {
  position: absolute;
  top: 16px;
  right: 16px; }

.flat-parameters {
  border-collapse: collapse; }

.flat-parameters__body {
  grid-template-columns: 1fr 1fr;
  row-gap: 12px;
  display: grid;
  column-gap: 20px; }
  @media (min-width: 768px) {
    .flat-parameters__body {
      max-width: 268px; } }
  @media (min-width: 1440px) {
    .flat-parameters__body {
      max-width: 410px;
      grid-template-columns: 1fr 1fr 1fr; } }

.flat-parameters__wrap {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap; }
  @media (min-width: 1440px) {
    .flat-parameters__wrap--finishing {
      grid-column: 3/4;
      grid-row: 1/2; } }

.flat-parameters__term {
  padding: 0;
  font-size: 12px;
  line-height: 16px;
  color: #989691; }

.flat-parameters__definition {
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px; }

.flat-parameters__sup {
  font-size: 8px; }

.answers {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: -4px -8px; }

.answers__item {
  margin: 4px 8px; }
